.btn {
  border-width: 0;
  background: transparent;
  text-align: center;
  cursor: pointer;
  position: relative;
  line-height: 1;
  display: inline-block;
  transition: all .3s ease;
}

.btn-white {
  min-width: 145px;
  height: 50px;
  line-height: 50px;
  padding: 0 10px;
  color: #fff;
  border: 2px solid #fff;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: normal;
  overflow: hidden;
  font-family: 'ceragr-regular', sans-serif;
}

.btn-white::after {
  content: '';
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 0;
  top: 50%;
  left: 50%;
  background: #fff;
  opacity: 0;
  transition: all 0.6s;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}

.btn-white span {
  position: relative;
  z-index: 4;
}

.btn-white:hover, .btn-white:active {
  color: #232525;
}

.btn-white:hover::after, .btn-white:active::after {
  height: 250px;
  opacity: 1;
}

.btn-offset {
  width: 191px;
  height: 55px;
  margin-left: 7px;
  border: 2px solid #212324;
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
  font-family: 'ceragr-regular', sans-serif;
}

.btn-offset:hover, .btn-offset:active, .btn-offset:focus {
  color: #fff;
}

.btn-offset:hover .btn-offset-text, .btn-offset:active .btn-offset-text, .btn-offset:focus .btn-offset-text {
  background-color: #33ddfb;
  transform: translate(0, 0);
}

.btn-offset-text {
  width: 190px;
  height: 55px;
  line-height: 53px;
  border: 2px solid #33ddfb;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-8px, -10px);
  transition: all 0.5s ease;
}

.hide-placeholder:-moz-placeholder {
  opacity: 1;
  color: #a6a6a6;
  font-size: 17px;
  transition: opacity 0.3s ease;
  font-family: 'ceragr-light', sans-serif;
}

.hide-placeholder::-moz-placeholder {
  opacity: 1;
  color: #a6a6a6;
  font-size: 17px;
  transition: opacity 0.3s ease;
  font-family: 'ceragr-light', sans-serif;
}

.hide-placeholder:-ms-input-placeholder {
  opacity: 1;
  color: #a6a6a6;
  font-size: 17px;
  transition: opacity 0.3s ease;
  font-family: 'ceragr-light', sans-serif;
}

.hide-placeholder::-webkit-input-placeholder {
  opacity: 1;
  color: #a6a6a6;
  font-size: 17px;
  transition: opacity 0.3s ease;
  font-family: 'ceragr-light', sans-serif;
}

.hide-placeholder:focus:-moz-placeholder {
  opacity: 0;
}

.hide-placeholder:focus::-moz-placeholder {
  opacity: 0;
}

.hide-placeholder:focus:-ms-input-placeholder {
  opacity: 0;
}

.hide-placeholder:focus::-webkit-input-placeholder {
  opacity: 0;
}

.form-group {
  margin-bottom: 15px;
  position: relative;
}

.form-group::after {
  width: 0;
  height: 2px;
  position: relative;
  top: -2px;
  display: block;
  content: '';
  border-bottom: 2px solid #33ddfb;
  transition: all 0.3s ease;
}

.form-group-focus::after {
  width: 100%;
}

.form-control {
