  background-color: transparent;
  font-weight: normal;
  display: block;
  transition: all .3s ease;
  font-family: 'ceragr-light', sans-serif;
}

textarea.form-control {
  height: 188px;
  padding: 19px 47px 10px;
  resize: none;
  overflow: hidden;
}

