html,
body {
  width: 100%;
  height: 100%;
  background: #1b1d1e;
  color: #e6e6e6;
  font-size: 16px;
  line-height: 1.4;
  @include CeraGRLight;
}

body {
  position: relative;
  &:not(.page)::before {
      background: url(../img/bg/bg-cases@2x.jpg) no-repeat top left;
      background-size: cover;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      position: fixed;
      will-change: transform;
      content: '';
      pointer-events: none;
  }
}

.page:not(.portfolio)::before {
  background: url(../img/bg/bg-page@2x.jpg) no-repeat top left;
  background-size: cover;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: fixed;
  will-change: transform;
  content: '';
  pointer-events: none;
}

a, .as-link {
  color: #33ddfb;
  text-decoration: none;
  transition: all 0.3s ease;
  &:hover {
    color: #fff;
  }
  &:focus {
    outline: none;
  }
}

.as-link {
  cursor: pointer;
}

b, strong {
  font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  padding-top: 0;
  font-weight: normal;
}

.text {
  &-center {
    text-align: center
  }
  &-left {
    text-align: left
  }
  &-right {
    text-align: right
  }
  &-justify {
    text-align: justify
  }
  &-bold {
    font-weight: normal;
  }
  &-nowrap {
    white-space: nowrap;
  }
  &-lowercase {
    text-transform: lowercase;
  }
  &-uppercase {
    text-transform: uppercase;
  }
  &-capitalize {
    text-transform: capitalize;
  }
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.list {
  &-unstyled {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
  }
}

.clearfix {
  @include clearfix;
}

button, input, select, textarea {
  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }
}

.container {
  width: 100%;
  max-width: 1490px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 5.4%;
  padding-right: 5.4%;
}

.wrapper {
  padding: 0 90px 0 154px;
  background: url(../img/bg/bg-page@2x.png) no-repeat center top;
  background-size: cover;
}

.page-content {
  font-size: 21px;
  text-align: center;
  color: #e6e6e6;
  line-height: 1.3;
  position: relative;
  z-index: 5;
  p {
    margin-bottom: 20px;
  }
}

.support-block {
  display: flex;
  align-items: center;
  .social {
    align-self: center;
  }
  .btn-white {
    letter-spacing: 1px;
  }
}

.social {
  height: 30px;
  margin-right: 38px;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  li {
    padding-top: 6px;
    display: inline-block;
    vertical-align: top;
    &:first-child {
      padding-top: 4px;
      margin-right: 29px;
    }
  }
  a:hover svg {
    fill: #33ddfb;
  }
  svg {
    fill: #fff;
    transition: all 0.3s ease;
  }
  .icon-be {
    width: 24px;
    height: 24px;
  }
  .icon-fb {
    width: 18px;
    height: 18px;
  }
}

.title-block {
  margin-bottom: 0;
  z-index: 5;
  position: relative;
  display: flex;
  align-items: center;
  &::before {
    width: 50px;
    height: 1px;
    margin-right: 25px;
    content: '';
    background-color: #33ddfb;
  }
  img {
    max-height: 83px;
  }
}

.section {
  padding: 0 90px 0 154px;
}

.title-line-bottom {
  margin: 0 0 96px;
  font-size: 26px;
  color: rgb(230, 230, 230);
  line-height: 1.2;
  text-align: center;
  @include CeraGRLight;
  span {
    display: block;
    max-width: 98%;
    margin: 0 auto 10px;
  }
  &::after {
    width: 165px;
    height: 1px;
    background-color: #33ddfb;
    display: inline-block;
    content: '';
  }
}

.list-line {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  font-size: 23px;
  li {
    padding-left: 45px;
    position: relative;
    @include CeraGRRegular;
    &:not(:last-child) {
      margin-bottom: 23px;
    }
    &::before {
      width: 22px;
      height: 1px;
      top: 14px;
      left: 0;
      background-color: #fff;
      position: absolute;
      content: '';
    }
  }
}

.next-layer {
  position: absolute;
  bottom: 69px;
  z-index: 10;
  margin-left: -58px;
  left: 50%;
  &::before {
    width: 1px;
    height: 50px;
    content: '';
    position: absolute;
    top: -16px;
    left: 11px;
    background-color: #33ddfb;
    z-index: 5;
  }
  .arrow {
    width: 32px;
    height: 32px;
    fill: #33ddfb;
    position: absolute;
    bottom: -35px;
    left: -4px;
    transition: all 0.3s ease;
  }
  &:hover {
    .next-layer-text {
      color: #33ddfb;
    }
    .arrow {
      bottom: -30px;
    }
  }
}

.next-layer-text {
  color: #e6e6e6;
  font-size: 30px;
  line-height: 1;
  display: inline-block;
  text-transform: uppercase;
  position: relative;
  z-index: 9;
  transition: all 0.3s ease;
  @include CeraGRMedium;
}
